.app-wrapper {
  display: grid;
  margin: 0 auto;
  grid-template-areas:
    'Header Header '
    'NavBar MainContent ';
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-row-gap: 20px;
  grid-column-gap: 20px;
  /* grid-gap: 10px; */
}
.app-wrapper-content{
  border-radius: 35px;
  grid-area: MainContent;
  text-align: center;
  justify-content: center;
  margin: 0 auto;
  width: 100%;
  padding-bottom: 30px;
  background-color: #d6efad;
}